<template>
<div class="test2">
    <img v-for="(item, index) in baseItems" :key="index"
      :style="'width:100px; height:100px; left:' + item.left + 'px; top:' + item.top + 'px'"
      :src="this.$root.IMG_PATH + 'sfv/' + item.path" :id="item.id" :class="itemClass(item)"
      @mousedown.prevent="touchStart($event)" @touchstart.prevent="touchStart($event)"
      @touchmove.prevent="touchMove($event)" @mousemove.prevent="touchMove($event)"
      @mouseup.prevent="touchEnd($event)" @touchend.prevent="touchEnd($event)"
      @mouseleave.prevent="touchLeave($event)" @touchleave.prevent="touchLeave($event)">

  <div id="chartParent" style="width:700px; height:500px; background-color:black; margin:auto; position:relative">
    <!-- <div style="width:100px; height:100px; background-color:blue; position: absolute; left: 120px; top:0px"
    @mousedown="touchStart($event)" @touchstart="touchStart($event)"
    @touchmove="touchMove($event)" @mousemove="touchMove($event)"
    @mouseup="touchEnd($event)" @touchend="touchEnd($event)"
    @mouseleave="touchLeave($event)" @touchleave="touchLeave($event)"/> -->
  </div>
  <div style="height:10px" />
  <button type="button" class="btnBase font_l" style="width:200px; padding:10px 10px; margin-left:10px" v-on:click="pushEditBtn('BACK')">戻す</button>
  <button type="button" class="btnBase font_l" style="width:200px; padding:10px 10px; margin-left:10px" v-on:click="pushEditBtn('BACK_ALL')">全て戻す</button>
  <div style="height:30px" />
  <div id="baseParent" style="background-color:white; padding:10px; display:flex; flex-wrap:wrap">
  </div>
  <button type="button" v-on:click="test">aaa</button>
</div>
</template>

<script>

export default {
  name: 'Test2',
  components: {
  },
  data() {
    return {
      x : 0,
      y : 0,
      isTouch : false,
      targetEl : null,
      targetItem : null,
      chartItems : [],
      baseItems : [],

    };
  },
  computed : {
    itemClass : function () {
      return (item) => {
        let retVal = "";
        if (this.targetItem != null && item.id == this.targetItem.id) {
          retVal = "selectItem ";
        }
        if (item.isBase) {
          return retVal + "itemBase";
        } else {
          return retVal + "itemChart";
        }
      }
    }
  },
  mounted () {
    this.$nextTick(function () {
      // ビュー全体がレンダリングされた後にのみ実行されるコード
      let baseParent = document.getElementById("baseParent");
      let chartParent = document.getElementById("chartParent");

      let baseList = Array.from(document.getElementsByClassName("itemBase"));
      let chartList = Array.from(document.getElementsByClassName("itemChart"));
      console.log(baseList);
      console.log(chartList);

      for (let i = 0; i < baseList.length; i++) {
        const element = baseList[i];
        baseParent.appendChild(element);
      }
      for (let i = 0; i < chartList.length; i++) {
        const element = chartList[i];
        chartParent.appendChild(element);
      }
    })
  },
  methods : {
    test : function() {
      console.log(document.getElementById("1"));
    },
    pushEditBtn : function(name) {
      if (name == "BACK") {
        if (this.targetEl == null) {
          return;
        }

        document.getElementById("baseParent").append(this.targetEl)
        this.targetItem.isBase = true;
        this.targetEl = null;
        this.targetItem = null;
      } else if (name == "BACK_ALL") {
        let chartList = Array.from(document.getElementsByClassName("itemChart"));
        let baseParent = document.getElementById("baseParent");
        for (let i = 0; i < chartList.length; i++) {
          const element = chartList[i];
          baseParent.appendChild(element);
        }
        for (let i = 0; i < this.baseItems.length; i++) {
          this.baseItems[i].isBase = true;
        }
        this.targetEl = null;
        this.targetItem = null;
      }
    },
    touchStart : function(e){
      // console.log(e);
      this.targetEl = e.target;
      // console.log(this.targetEl);
      
      var event = null;
      if(e.type == "mousedown") {
        event = e;
      } else if (e.type == "touchstart") {
        event = e.touches[0];
      }
      
      // console.log(event.pageX + " " + this.targetEl.offsetLeft);
      this.x = event.pageX - this.targetEl.offsetLeft;
      this.y = event.pageY - this.targetEl.offsetTop;
      // console.log("touch start:%d,%d", this.x, this.y);
      console.log(event.pageX);
      console.log(this.targetEl.offsetLeft);
      console.log(this.x);

      let id = e.target.id;
      for (let i = 0; i < this.baseItems.length; i++) {
        if (id == this.baseItems[i].id) {
          this.targetItem = this.baseItems[i];
          break;
        }        
      }
      this.isTouch = true;
    },
    touchMove : function(e){
      if (!this.isTouch) {
        return;
      }
      if (this.targetItem.isBase) {
        //ベースから移行
        this.targetItem.isBase = false;
        document.getElementById("chartParent").appendChild(this.targetEl);
        var clientRect = document.getElementById("chartParent").getBoundingClientRect() ;
        var x = window.pageXOffset + clientRect.left;
        this.x = x + 50;

        var y = window.pageYOffset + clientRect.top;
        this.y = y + 50;
        // console.log(this.targetEl.style.top + " " + this.targetEl.style.left);
        // console.log(e);
        // this.x = window.innerWidth / 2;
        // this.y = 75;
      }

      var event = null;
      if(e.type == "mousemove") {
        event = e;
      } else if (e.type == "touchmove") {
        event = e.touches[0];
      }
      this.targetEl.style.top = event.pageY - this.y + "px";
      this.targetEl.style.left = event.pageX - this.x + "px";

      if (parseInt(this.targetEl.style.top) < 0) {
        this.targetEl.style.top = "0px";
      }
      if (parseInt(this.targetEl.style.left) < 0) {
        this.targetEl.style.left = "0px";
      }
      if (parseInt(this.targetEl.style.top) > 400) {
        this.targetEl.style.top = "400px";
      }
      if (parseInt(this.targetEl.style.left) > 600) {
        this.targetEl.style.left = "600px";
      }
    },
    touchEnd : function(){
      if (!this.isTouch) {
        return;
      }
      // console.log("touch end:%d,%d", e.offsetX, e.offsetY);
      this.isTouch = false;
    },
    touchLeave : function(){
      if (!this.isTouch) {
        return;
      }
      // console.log("touch leave:%d,%d", e.offsetX, e.offsetY);
      this.isTouch = false;
    },
    initAction : function() {
    this.baseItems = [
        {id:'sfv01', path:'sfv_icon01.png', left:0, top:0, isBase:true},
        {id:'sfv02', path:'sfv_icon02.png', left:0, top:0, isBase:true},
        {id:'sfv03', path:'sfv_icon03.png', left:0, top:0, isBase:true},
        {id:'sfv04', path:'sfv_icon04.png', left:0, top:0, isBase:true},
        {id:'sfv05', path:'sfv_icon05.png', left:0, top:0, isBase:true},
        {id:'sfv06', path:'sfv_icon06.png', left:0, top:0, isBase:true},
        {id:'sfv07', path:'sfv_icon07.png', left:0, top:0, isBase:true},
        {id:'sfv08', path:'sfv_icon08.png', left:0, top:0, isBase:true},
        {id:'sfv09', path:'sfv_icon09.png', left:0, top:0, isBase:true},
        {id:'sfv10', path:'sfv_icon10.png', left:0, top:0, isBase:true},
        {id:'sfv11', path:'sfv_icon11.png', left:0, top:0, isBase:true},
        {id:'sfv12', path:'sfv_icon12.png', left:0, top:0, isBase:true},
        {id:'sfv13', path:'sfv_icon13.png', left:0, top:0, isBase:true},
        {id:'sfv14', path:'sfv_icon14.png', left:0, top:0, isBase:true},
        {id:'sfv15', path:'sfv_icon15.png', left:110, top:0, isBase:false},
        {id:'sfv16', path:'sfv_icon16.png', left:210, top:0, isBase:false},
        {id:'sfv17', path:'sfv_icon17.png', left:310, top:0, isBase:false},
        {id:'sfv18', path:'sfv_icon18.png', left:410, top:0, isBase:false},
      ];
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.initAction();
    });
  },
  beforeRouteUpdate(to, from, next) {
    next(vm => {
      vm.initAction();
    });
  },
}
</script>

<style>

.itemChart {
  position : absolute;
}

.itemBase {
  position : static;
}

.selectItem {
  border : 2px solid red;
}
</style>