<template>
  <!-- <div class="header bgColorHF" style="text-align:right; z-index:999">
    <div class="container-fluid">
      <div class="row bgColorHF" style="height:50px;">
        <div class="col-6" style="text-align:left">
          ティア表（仮）
        </div>
        <div class="col-6" style="justify-content:right; display:flex; align-items:center;">
        </div>
      </div>
    </div>
    <div class="bgColorLine" style="height:2px" />
  </div> -->
  <router-view/>
    <!-- <div class="bgColorHF"  style="padding: 30px 0;">
    </div> -->

  <div id="overlay" v-show="this.isPopup"></div>

</template>

<script>
import { getAuth, signOut } from 'firebase/auth'
import axios from 'axios'
  
  export default {
    data() {
      return {
        API_SUCCESS : 200,
        API_FAILED_LOGIN : 99,
        API_FAILED_AUTH : 100,
        API_FAILED_PARAM : 300,
        API_FAILED_NODATA : 400,
        API_FAILED_DUPLICATE : 401,
        API_FAILED_MISMATCH : 402,
        API_FAILED_PRIVATE : 500,
        API_FAILED_FILE : 600,
        API_FAILED_EXEPTION : 900,

        isLogin: false,
        userId : "",
        displayName : "",
        twitterId : "",

        IMG_PATH : "/ToriTori/app/img/",
        ICON_PATH : "/ToriTori/storage/icon/",
        CARD_PATH : "/ToriTori/storage/card/",

        // SERVER_PATH : "http://localhost:8002/ToriTori/api/",
        // ROOT_PATH : "/ToriTori/",
        // DOMAIN_PATH : "localhost",

        // SERVER_PATH : "https://iphone-taro.sakura.ne.jp/ToriTori/api/",
        // ROOT_PATH : "/ToriTori/",
        // DOMAIN_PATH : "iphone-taro.sakura.ne.jp",
        
        pageName : "",
        reloadValue : "", //画像の更新用,

        mainBg : "backgroundBase",

        mainBgs : {
          "normal" : "backgroundBase",
          "correct" : "backgroundCorrect",
          "incorrect" : "backgroundIncorrect",
        },

        isPopup: false,
        popupKbn: "",
        popupTitle: "",
        popupBody: "",
        popupBodyClass : "popupBodyLeft",
      }
    },
    computed : {
      snsIcon () {
        return (url)=>{
          if (url == null) {
            return "";
          }

          url = url.toLowerCase();
          if (url.indexOf("youtube") > -1) {
            return "icon_youtube";
          } else if (url.indexOf("twitter") > -1) {
            return "icon_twitter";
          } else if (url.indexOf("twitch") > -1) {
            return "icon_twitch";
          }
          return "icon_page";
        }
      },
    },
    methods : {
      showLoad: function() {
        this.showPopup("LOAD", "", "");
      },
      showPopup: function(kbn, title, body) {
        document.getElementById('body').className = "modalBg";
        this.popupKbn = kbn;
        this.popupTitle = title;
        this.popupBody = body;
        this.isPopup = true;
      },
      pushPopupBg : function () {
        if (this.popupKbn == "TIPS") {
          document.getElementById('body').className = "";
          this.isPopup = false;
        }        
      },
      closePopup : function () {
        document.getElementById('body').className = "";
        this.isPopup = false;
      },
      checkServerResponse : function (response) {
        //サーバー返答時の共通処理
        console.log(response.data);
        let resData = response.data;
        if (resData["status"] == this.API_FAILED_AUTH) {
          console.log("未認証des"); 
          //未認証エラー
          this.isLogin = false;
          this.$router.push("/");
          return false;
        } else if (resData["status"] != this.API_SUCCESS) {
          //エラー
          this.$router.push({path : "/error", query : {errorCode : resData["status"], message : resData["errMsg"]}});
          return false;
        }

        if (resData["isLogin"] == "") {
          //未ログイン
          this.isLogin = false;
        } else {
          this.isLogin = true;
          this.userId = resData["isLogin"];
          this.twitterId = localStorage.twitterId;
          this.displayName = localStorage.displayName
        }
        console.log("BBBBBBBBBBBBB");
        return true;
      },
      pushLogout : function () {
          axios.get(this.$root.SERVER_PATH + 'logout')
          .then((response) => {
            let resData = response.data;
            console.log(resData["status"] );
          
            if (!this.$root.checkServerResponse(response)) {
              return;
            }
            
            signOut(getAuth());
            // this.userData = {
            //   userId : "",
            //   userName : "",
            //   profile : "",
            // };
            // this.$router.push('/');
            this.$router.go({name: "Top"});
            this.isLogin = false;
          })
          .catch((e) => {
            alert(e);
          });

      },
      pushTag : function (val) {
			this.$router.push({name: "Top", query : { keyword: val, order: '0'}});
      },
      pushHome : function () {
        if (this.$root.pageName == "Top") {
          location.reload();
        } else {
          this.$router.push("/");
        }
      },
    }
  }
</script>



