<template>
<br>
<button type="button" class="btnBase font_m" style="padding:10px 10px; margin-left:10px" v-on:click="show">表示</button>
<br>

<div class="test">
  <div v-show="isEditTitle" style="display:flex; justify-content:center">
    <input type="text" style="width:60%; height:60px" class="form-control bo2 font_l" v-model="title" maxlength="30" placeholder="タイトルを入力してください">
    <button type="button" class="btnBase font_l" style="padding:10px 10px; margin-left:10px" v-on:click="pushTitleBtn('OK')">OK</button>
  </div>
  <div v-show="!isEditTitle">
    <span class="font_l">{{ title }}</span>
    <button type="button" class="btnBase font_l" style="padding:10px 10px; margin-left:10px" v-on:click="pushTitleBtn('EDIT')">編集</button>
  </div>
  <div style="width:95%">
    <div v-for="(catData, index) in categoryList" :key="index">
      <div style="display:flex; min-height:100px; border:2px solid white;" :style="'background-color:' + categoryColorList[catData.color]">
        <div style="width:100px; display:flex; align-items:center">
          <p style="width:100%; margin:0; color:black">{{ catData['name']}}</p>
        </div>
        <div id="box1" class="box" style="text-align:left; width:100%; background-color:black">
          <draggable v-model="catData['items']" item-key="no" group="ITEMS" class="dRow" style="background-color:black; padding:10px" @end="onEnd">
            <template #item="{ element }">
              <img :src="this.$root.IMG_PATH + 'sfv/' + element.path" style="width:100px">
            </template>
          </draggable>
        </div>
        <div style="width:100px; text-align:center">
          <button type="button" class="btnBase font_s" style="width:80%; padding:10px 10px;" v-on:click="pushSetting(index)">設定</button>
          <button v-show="index != 0" type="button" class="btnBase font_s" style="width:80%; padding:10px 10px;" v-on:click="chOrder(0, index)">上</button>
          <button v-show="index != categoryList.length - 1" type="button" class="btnBase font_s" style="width:80%; padding:10px 10px;" v-on:click="chOrder(1, index)">下</button>
        </div>
      </div>
    </div>
    <div id="box2" class="box">
      <draggable v-model="baseItems" item-key="no" group="ITEMS" style="padding:10px;" @end="onEnd">
        <template #item="{ element }">
          <img :src="this.$root.IMG_PATH + 'sfv/' + element.path" style="width:100px">
        </template>
      </draggable>
    </div>
  </div>
</div>
<button type="button" class="btnBase font_l" style="padding:10px 10px; margin-left:10px" v-on:click="pushBtn('RESET')">リセット</button>
<button type="button" class="btnBase font_l" style="padding:10px 10px; margin-left:10px" v-on:click="pushBtn('SAVE_SCREEN')">保存</button>
<div class="sp_line_m" />
<button type="button" class="btnBase font_l" style="padding:10px 10px; margin-left:10px" v-on:click="pushBtn('SHARE')">ツイッターにシェア</button>
<div class="sp_line_ss" />
※画像を添付したい場合は、一度保存して、シェアボタンから画像を添付してください。


<!-- スクショ用 -->
<div id="screen" v-show="this.isShowScreen" style="position:absolute; left:20px; top:100px; width:1200px; background-color:#b8e994">
  <span class="font_l">{{ title }}</span>
  <div v-for="(catData, index) in categoryList" :key="index">
    <div style="display:flex; min-height:100px; border:2px solid white;" :style="'background-color:' + categoryColorList[catData.color]">
      <div style="width:100px; display:flex; align-items:center">
        <p style="width:100%; margin:0; color:black">{{ catData['name']}}</p>
      </div>
      <div id="box1" class="box" style="text-align:left; width:100%; background-color:black">
        <draggable v-model="catData['items']" item-key="no" group="ITEMS" class="dRow" style="background-color:black; padding:10px" @end="onEnd">
          <template #item="{ element }">
            <img :src="this.$root.IMG_PATH + 'sfv/' + element.path" style="width:100px">
          </template>
        </draggable>
      </div>
    </div>
  </div>
</div>

<div id="overlay" v-show="isShowSetting" v-on:click="closeOverlay($event)">
  <div id="content" style="text-align:center;" class="popupA settingWidth">
    <div style="text-align:center">
      <p class="font_xl">カテゴリー色</p>
      <div style="display:flex; justify-content: center; flex-wrap:wrap">
        <div v-for="(color, index) in categoryColorList" :key="index" :style="'width:50px; height:50px; margin:5px; border-radius:50%; background-color:' + color" :class="selectedColor(index)" v-on:click="pushSettingColor(index)" />
      </div>
      <div class="sp_line_l" />
      <p class="font_xl">カテゴリー名</p>
      <input type="text" style="width:80%; margin:auto" class="form-control bo2" v-model="setName" maxlength="10" placeholder="10文字以内">
      <div class="sp_line_l" />
      <button type="button" class="btnBase font_l" style="width:50%; padding:10px 10px;" v-on:click="pushSettingBtn('SAVE')">保存</button>
      <div class="sp_line_xl" />
      <button type="button" class="btnBase font_l" style="width:40%; padding:10px 10px; margin-right:10px" v-on:click="pushSettingBtn('DELETE')">削除</button>
      <button type="button" class="btnBase font_l" style="width:40%; padding:10px 10px; margin-left:10px" v-on:click="pushSettingBtn('CLEAR')">中身を戻す</button>
      <div class="sp_line_l" />
      <button type="button" class="btnBase font_l" style="width:40%; padding:10px 10px; margin-right:10px" v-on:click="pushSettingBtn('ADD_UP')">この上に追加</button>
      <button type="button" class="btnBase font_l" style="width:40%; padding:10px 10px; margin-left:10px" v-on:click="pushSettingBtn('ADD_DOWN')">この下に追加</button>
    </div>
  </div>
</div>
</template>

<script>
import draggable from 'vuedraggable'

export default {
  name: 'Test',
  components: {
    draggable
  },
  data() {
    return {
      selSettingIndex : 0,
      isShowSetting : false,
      setName : "",
      setColorIndex : 0,

      title : "タイトル未設定",
      isEditTitle : false,

      categoryColorList : [
        "#00b894", "#00cec9", "#0984e3", "#6c5ce7", "#fdcb6e", "#e17055", "#d63031", "#e84393"
      ],

      categoryList : [
        {name:"S", color:0, items:[]},
        {name:"A", color:1, items:[]},
        {name:"B", color:2, items:[]},
        {name:"C", color:3, items:[]},
        {name:"D", color:4, items:[]},
      ],
      baseItems: [],

      isShowScreen : false,
    };
  },
  computed : {
		selectedColor () {
      return (index) => {
        if (index == this.setColorIndex) {
          return "setSelectedColor";
        } else {
          return "";
        }
			}
    }
  },
  mounted () {
  },
  methods : {
    show : function () {
      this.isShowScreen = !this.isShowScreen;
    },
    pushBtn : function(name) {
      if (name == "SAVE_SCREEN") {
        //画像化
        window.html2canvas(document.getElementById('screen'), {
          scale:1
        }).then(canvas => {
          console.log(canvas.width + " " + canvas.height);
          // var base64 = canvas.toDataURL('image/jpeg');
          // base64 = base64.replace(/^.*,/, '');

          const a = document.createElement("a");
          a.href = canvas.toDataURL("image/jpeg", 1);
          a.download = "image";
          a.click();

          // axios.post(this.$root.SERVER_PATH + "updateChallenge", params);      
        });
      } else if (name == "RESET") {
        //全てリセット
        for (let i = 0; i < this.categoryList.length; i++) {
          let targetList = this.categoryList[i].items;

          for (let j = 0; j < targetList.length; j++) {
            let data = targetList[j];
            this.baseItems.push(data);
          }
          this.categoryList[i].items = [];
        }
      } else if (name == "SHARE") {
        let bodyStr = "【とりとりメーカー】" + this.title;
        let siteUrl = "http://www.yahoo.co.jp";
        var url = "https://twitter.com/intent/tweet?" + 
        "hashtags=" + encodeURIComponent('toritori') + 
        "&text=" + encodeURIComponent(bodyStr) + 
        "&url=" + encodeURIComponent(siteUrl);
        // "&via=" + encodeURIComponent("iphonetaro_live");
        window.open(url);
      }
    },
    pushTitleBtn : function (kbn) {
      if (kbn == "EDIT") {
        this.isEditTitle = true;
      } else if (kbn == "OK") {
        //保存
        this.title = this.title.trim();
        if (this.title == "") {
          this.title = "タイトル未設定";
        }

        this.onEnd();
        this.isEditTitle = false;
      }
    },
    pushSetting : function (index) {
      console.log(index);
      this.selSettingIndex = index;
      this.setName = this.categoryList[index].name;
      this.setColorIndex = this.categoryList[index].color;

      this.isShowSetting = true;
    },
    pushSettingBtn : function (name) {
      if (name == "SAVE") {
        //保存
        this.categoryList[this.selSettingIndex].name = this.setName;
        this.categoryList[this.selSettingIndex].color = this.setColorIndex;
        this.isShowSetting = false;
      } else if (name == "DELETE") {
        //カテゴリー削除
        //まず中身を戻す
        let targetList = this.categoryList[this.selSettingIndex].items;
        for (let i = 0; i < targetList.length; i++) {
          let data = targetList[i];
          this.baseItems.push(data);
        }

        //カテゴリーを削除
        this.categoryList.splice(this.selSettingIndex, 1);

        this.isShowSetting = false;
      } else if (name == "CLEAR") {
        //中身を戻す
        let targetList = this.categoryList[this.selSettingIndex].items;
        for (let i = 0; i < targetList.length; i++) {
          let data = targetList[i];
          this.baseItems.push(data);
        }
        this.categoryList[this.selSettingIndex].items = [];
      } else if (name == "ADD_UP" || name == "ADD_DOWN") {
        //データ保存
        this.categoryList[this.selSettingIndex].name = this.setName;
        this.categoryList[this.selSettingIndex].color = this.setColorIndex;

        let addIndex = this.selSettingIndex;
        if (name == "ADD_DOWN") {
          addIndex = this.selSettingIndex + 1;
        }
        
        //カテゴリを追加
        let after = this.categoryList.splice(addIndex);
        Array.prototype.push.apply(this.categoryList, [{name:"NEW", color:0, items:[]}]);
        Array.prototype.push.apply(this.categoryList, after);

        //アイテムを追加
        this.isShowSetting = false;
      }
    },
    pushSettingColor : function (index) {
      this.setColorIndex = index;
    },
    closeSetting: function() {
			// document.getElementById('body').className = "";
			this.isShowModal = false;
		},
		closeOverlay : function (e) {
			if (e.target.id == "overlay") {
        this.categoryList[this.selSettingIndex].name = this.setName;
        this.categoryList[this.selSettingIndex].color = this.setColorIndex;
				this.isShowSetting = false;
			}
		},
    onEnd : function(originalEvent) {
      console.log(originalEvent);
      //現在の配置を保存
      let saveArray = {title: this.title, category:this.categoryList, base:this.baseItems};
      let json_text = JSON.stringify(saveArray);
      console.log(json_text);
      localStorage.setItem("toritori_sfv", json_text);
    },
    chOrder : function (kbn, index) {
      //順序移動
      let before = index;
      let after = index;
      if (kbn == 0) {
        //上
        after = index - 1;
      } else {
        //下
        after = index + 1;
      }
      const value = this.categoryList[before];
      const tail = this.categoryList.slice(before + 1);
      this.categoryList.splice(before);
      Array.prototype.push.apply(this.categoryList, tail);
      this.categoryList.splice(after, 0, value);
    },
    getOptions : function() {
      return this.options;
    },
    test : function () {
    },
    initAction : function () {
      //仮取得データ
      let getItems = [
        {path:'sfv_icon01.png', name:'', categoryNo:''},
        {path:'sfv_icon02.png', name:'', categoryNo:''},
        {path:'sfv_icon03.png', name:'', categoryNo:''},
        {path:'sfv_icon04.png', name:'', categoryNo:''},
        {path:'sfv_icon05.png', name:'', categoryNo:''},
        {path:'sfv_icon06.png', name:'', categoryNo:''},
        {path:'sfv_icon07.png', name:'', categoryNo:''},
        {path:'sfv_icon08.png', name:'', categoryNo:''},
        {path:'sfv_icon09.png', name:'', categoryNo:''},
        {path:'sfv_icon10.png', name:'', categoryNo:''},
        {path:'sfv_icon11.png', name:'', categoryNo:''},
        {path:'sfv_icon12.png', name:'', categoryNo:''},
        {path:'sfv_icon13.png', name:'', categoryNo:''},
        {path:'sfv_icon14.png', name:'', categoryNo:''},
        {path:'sfv_icon15.png', name:'', categoryNo:''},
        {path:'sfv_icon16.png', name:'', categoryNo:''},
        {path:'sfv_icon17.png', name:'', categoryNo:''},
        {path:'sfv_icon18.png', name:'', categoryNo:''},
        {path:'sfv_icon19.png', name:'', categoryNo:''},
        {path:'sfv_icon20.png', name:'', categoryNo:''},
        {path:'sfv_icon21.png', name:'', categoryNo:''},
        {path:'sfv_icon22.png', name:'', categoryNo:''},
        {path:'sfv_icon23.png', name:'', categoryNo:''},
        {path:'sfv_icon24.png', name:'', categoryNo:''},
        {path:'sfv_icon25.png', name:'', categoryNo:''},
        {path:'sfv_icon26.png', name:'', categoryNo:''},
        {path:'sfv_icon27.png', name:'', categoryNo:''},
        {path:'sfv_icon28.png', name:'', categoryNo:''},
        {path:'sfv_icon29.png', name:'', categoryNo:''},
        {path:'sfv_icon30.png', name:'', categoryNo:''},
        {path:'sfv_icon31.png', name:'', categoryNo:''},
        {path:'sfv_icon32.png', name:'', categoryNo:''},
        {path:'sfv_icon33.png', name:'', categoryNo:''},
        {path:'sfv_icon34.png', name:'', categoryNo:''},
        {path:'sfv_icon35.png', name:'', categoryNo:''},
        {path:'sfv_icon36.png', name:'', categoryNo:''},
        {path:'sfv_icon37.png', name:'', categoryNo:''},
        {path:'sfv_icon38.png', name:'', categoryNo:''},
        {path:'sfv_icon39.png', name:'', categoryNo:''},
        {path:'sfv_icon40.png', name:'', categoryNo:''},
        {path:'sfv_icon41.png', name:'', categoryNo:''},
      ];

      //保存してある情報を取得
      let saveStr = localStorage.getItem("toritori_sfv");
      let saveArray = {};
      if (saveStr == null) {
        //なし デフォルトを設定？
        this.baseItems = getItems;
      } else {
        saveArray = JSON.parse(saveStr);
        this.title = saveArray["title"];
        this.categoryList = saveArray["category"];
        this.baseItems = saveArray["base"];

        //データベース上のデータとすり合わせ（追加・削除）
        //保存データにない場合追加
        for (let i = 0; i < getItems.length; i++) {
          const indexName = getItems[i].path;

          let existFlg = false;
          //カテゴリーにあるか
          for (let j = 0; j < this.categoryList.length; j++) {
            const catData = this.categoryList[j];
            const items = catData.items;
            for (let k = 0; k < items.length; k++) {
              const cIndexName = items[k].path;
              if (indexName == cIndexName) {
                //存在する
                existFlg = true;
                break;
              }
            }
          }
          //ベースにあるか
          if (!existFlg) {
            for (let j = 0; j < this.baseItems.length; j++) {
              const bIndexName = this.baseItems[j].path;
              if (indexName == bIndexName) {
                //存在する
                existFlg = true;
                break;
              }
            }
          }

          //ない場合はベースに追加
          if (!existFlg) {
            console.log("追加 " + getItems[i].path);
            this.baseItems.push(getItems[i]);
          }
        }

        //保存データにしかない場合は削除
        //カテゴリー
        for (let i = 0; i < this.categoryList.length; i++) {
          const catData = this.categoryList[i];
          const items = catData.items;
          for (let j = 0; j < items.length;) {
            const cIndexName = items[j].path;
            let existFlg = false;
            for (let k = 0; k < getItems.length; k++) {
              const indexName = getItems[k].path;
              if (cIndexName == indexName) {
                //存在する
                existFlg = true;
                break;
              }
            }
            
            if (!existFlg) {
              //該当行を削除
              console.log("削除C " + items[j].path);
              items.splice(j, 1);
            } else {
              j++;
            }
          }
        }
        //ベース
        for (let j = 0; j < this.baseItems.length;) {
            const bIndexName = this.baseItems[j].path;
            let existFlg = false;
            for (let k = 0; k < getItems.length; k++) {
              const indexName = getItems[k].path;
              if (bIndexName == indexName) {
                //存在する
                existFlg = true;
                break;
              }
            }
            
            if (!existFlg) {
              //該当行を削除
              console.log("削除B " + this.baseItems[j].path);
              this.baseItems.splice(j, 1);
            } else {
              j++;
            }
          }
      }


    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.initAction();
    });
  },
  beforeRouteUpdate(to, from, next) {
    next(vm => {
      vm.initAction();
    });
  },
}
</script>

<style>
.setSelectedColor {
  border : 3px solid red;
}

.settingWidth {
    width:95%;
}

.dRow {
  min-height:120px;
}

@media screen and (min-width : 992px) {
  .settingWidth {
      width:900px;
  }
}
</style>